.content-nav {
    overflow: hidden;
    height: 16px;
    position: relative;
    margin-left: -15px;
    margin-right: -15px;
    @media screen and (min-width: 600px) {
      height: 24px;
    }
    @media screen and (min-width: 990px) {
      height: auto;
      margin: 0;
    }
    &__wrapper {
      overflow: hidden;
      overflow-x: auto;
      height: 50px;
      @media screen and (min-width: 990px) {
        overflow: hidden;
        height: auto;
      }
    }
    &__list {
      display: flex;
      list-style: none;
      padding: 0;
      margin: 0;
      @media screen and (min-width: 990px) {
        border: 1px solid #B2B2B2;
        height: 42px;
      }
      @media screen and (min-width: 1200px) {
        display: inline-flex;
      }
    }
    &__item {
      position: relative;
      display: flex;
      flex: 0 0 auto;
      white-space: nowrap;
      padding-right: 30px;
      @media screen and (min-width: 600px) {
        padding-right: 50px;
      }
      @media screen and (min-width: 990px) {
        flex: 1 1 auto;
        white-space: normal;
        padding: 0;
      }
      @media screen and (min-width: 1200px) {
        flex: 0 1 auto;
      }
      &:last-child {
        padding-right: 15px;
        @media screen and (min-width: 990px) {
          padding-right: 0;
        }
      }
      &:first-child {
        padding-left: 15px;
        @media screen and (min-width: 990px) {
          padding-left: 0;
        }
      }
      &.active {
        z-index: 2;
      }
    }
    &__link {
      display: block;
      font: $regular 13px 'Montserrat', sans-serif;
      color: #C4C4C4;
      text-decoration: none;
      position: relative;
      @media screen and (min-width: 600px) {
        font-size: 16px;
      }
      @media screen and (min-width: 990px) {
        min-width: 130px;
        padding: 0 25px;
        text-align: center;
        height: 42px;
        align-items: center;
        justify-content: center;
        display: flex;
        flex: 1 1 auto;
        margin: -1px -1px 0;
      }
    }
    &__item.active &__link {
      color: $color-darkblue;
      @media screen and (min-width: 990px) {
        color: $color-white;
        background: $color-darkblue;
      }
    }
}