@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/gotham-pro-regular.woff") format("woff"), url("../fonts/gotham-pro-regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/gotham-pro-medium.woff") format("woff"), url("../fonts/gotham-pro-medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/gotham-pro-bold.woff") format("woff"), url("../fonts/gotham-pro-bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}

*,
:after,
:before {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  font: 400 18px/23px "Roboto", sans-serif;
  color: #000000;
  background: #FFFFFF;
}

a {
  color: #091C47;
  outline: none;
  transition: all 200ms ease;
}

a:hover,
a:active {
  text-decoration: none;
}

p {
  margin: 0 0 30px;
}

img {
  display: block;
  max-width: 100%;
  margin: 0;
}

strong,
b {
  font-weight: 700;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  line-height: normal;
  color: #091C47;
  margin: 0 0 30px;
}

h1,
.h1 {
  font-size: 28px;
}

h2,
.h2 {
  font-size: 26px;
}

h3,
.h3 {
  font-size: 24px;
}

h4,
.h4 {
  font-size: 23px;
}

h5,
.h5 {
  font-size: 22px;
}

h6,
.h6 {
  font-size: 18px;
}

ul,
ol {
  margin: 0 0 30px;
}

q {
  display: block;
  margin: 0 0 30px;
}

svg {
  display: block;
  overflow: hidden;
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #C4C4C4;
  opacity: 1;
}

input::-moz-placeholder,
textarea::-moz-placeholder {
  color: #C4C4C4;
  opacity: 1;
}

input:-moz-placeholder,
textarea:-moz-placeholder {
  color: #C4C4C4;
  opacity: 1;
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #C4C4C4;
  opacity: 1;
}

input::-ms-clear {
  display: none;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.cover {
  padding: 0 15px;
  position: relative;
  margin: 0 auto;
  min-width: 320px;
}

.slick-slider {
  display: block !important;
}

.slick-slide {
  outline: none;
}

.slick-slide > div {
  outline: none;
}

.fancybox-slide--html {
  padding: 15px;
}

.fancybox-bg {
  background: rgba(0, 0, 0, 0.31);
}

.fancybox-is-open .fancybox-bg {
  opacity: 1;
}

select {
  display: none;
}

.jq-selectbox {
  display: block !important;
  cursor: pointer;
}

.jq-selectbox select {
  display: block;
}

.jq-selectbox__select {
  height: 60px;
  padding: 0 62px 0 17px;
  border: 1px solid #ECEFEF;
  background: #FFFFFF;
  font: 400 16px "Roboto", sans-serif;
  color: #091C47;
}

.jq-selectbox.disabled .jq-selectbox__select {
  color: #DFDFDF;
}

.jq-selectbox__select-text {
  display: block;
  overflow: hidden;
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-top: 19px;
}

.jq-selectbox .placeholder {
  color: #DFDFDF;
}

.jq-selectbox__trigger {
  position: absolute;
  top: 0;
  right: 0;
  width: 62px;
  height: 100%;
}

.jq-selectbox__trigger-arrow {
  position: absolute;
  top: 22px;
  right: 24px;
  width: 10px;
  height: 10px;
  border-right: 3px solid #091C47;
  border-bottom: 3px solid #091C47;
  transform: rotate(45deg);
}

.jq-selectbox.disabled .jq-selectbox__trigger-arrow {
  border-color: #DFDFDF;
}

.jq-selectbox__dropdown {
  width: 100% !important;
  margin: -1px 0;
  padding: 0;
  border: 1px solid #ECEFEF;
  background: #FFFFFF;
  font: 400 16px "Roboto", sans-serif;
  left: 0 !important;
  top: 100% !important;
  right: auto !important;
  bottom: auto !important;
}

.jq-selectbox ul {
  margin: 0;
  padding: 0;
}

.jq-selectbox li {
  padding: 12px 17px;
  user-select: none;
  white-space: normal;
  color: #091C47;
}

.jq-selectbox li.selected {
  background-color: #ECEFEF;
}

.jq-selectbox li:hover {
  background-color: #ECEFEF;
}

.jq-selectbox li.disabled {
  color: #DFDFDF;
}

.jq-selectbox li.disabled:hover {
  background: none;
}

.logo {
  flex: 0 0 auto;
}

.logo__link {
  display: block;
  text-decoration: none;
}

.nav {
  flex: 1 1 auto;
}

.nav__list {
  list-style: none;
  padding: 0;
  margin: 0 -25px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}

.nav__item {
  padding: 5px 25px;
  font-size: 13px;
  line-height: normal;
  text-transform: uppercase;
}

.nav__link {
  color: #091C47;
  text-decoration: none;
}

.nav__link:hover,
.nav__link:active,
.active > .nav__link {
  color: #33ACEF;
}

.call {
  flex: 0 0 auto;
  font-size: 13px;
  line-height: normal;
}

.call__link {
  text-decoration: none;
  display: block;
  padding-left: 20px;
  position: relative;
  text-transform: uppercase;
}

.call__link:hover,
.call__link:active {
  color: #33ACEF;
}

.call__icon {
  width: 10px;
  height: 9px;
  fill: #091C47;
  transition: all 200ms ease;
  position: absolute;
  left: 0;
  top: 3px;
}

.call__link:hover .call__icon,
.call__link:active .call__icon {
  fill: #33ACEF;
}

.phone {
  flex: 0 0 auto;
  font-size: 13px;
  line-height: normal;
}

.phone__link {
  text-decoration: none;
  display: block;
  padding-left: 20px;
  position: relative;
  cursor: default;
}

.phone__icon {
  position: absolute;
  left: 0;
  top: 2px;
  fill: #091C47;
  width: 12px;
  height: 11px;
}

.pull {
  flex: 0 0 auto;
  width: 23px;
  height: 14px;
  position: relative;
  cursor: pointer;
}

.pull div {
  display: block;
  width: 23px;
  height: 2px;
  background: #091C47;
  position: absolute;
  left: 0;
  transition: all 200ms ease;
}

.pull div:nth-child(1) {
  top: 0;
}

.pull div:nth-child(2) {
  top: 6px;
  width: 18px;
}

.pull div:nth-child(3) {
  top: 12px;
}

.pull:hover div,
.pull:active div {
  background: #33ACEF;
}

.section-name {
  margin: 0;
  font: 500 28px "Roboto", sans-serif;
  color: #091C47;
  text-transform: uppercase;
  text-align: center;
  position: relative;
}

.section-name:before {
  content: "";
  display: none;
  width: 12px;
  height: 26px;
  background: #091C47;
  position: absolute;
  left: 0;
  top: 8px;
}

.section-name_inner {
  text-align: left;
}

.btn {
  display: inline-block;
  width: 100%;
  padding: 10px;
  text-decoration: none;
  outline: none;
  font: 700 18px "Roboto", sans-serif;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  border: 2px solid #091C47;
  background: none;
  transition: all 200ms ease;
}

.btn:hover,
.btn:active {
  color: #FFFFFF;
  background: #091C47;
}

.btn_fill {
  color: #FFFFFF;
  background: #091C47;
}

.btn_fill:hover,
.btn_fill:active {
  background: none;
  color: #091C47;
}

.news-names {
  display: none;
}

.news-names__item {
  display: block !important;
  width: 100% !important;
}

.news-names__item-name {
  font: 700 22px/26px "Roboto", sans-serif;
  text-transform: uppercase;
  color: #091C47;
  margin: 0;
  padding-left: 50px;
  position: relative;
}

.news-names__item-icon {
  width: 14px;
  height: 20px;
  position: absolute;
  left: 24px;
  top: 3px;
  fill: #091C47;
}

.news-carousel {
  display: none;
  position: relative;
}

.news-carousel__item {
  display: block !important;
  width: 100% !important;
  padding: 0 7.5px;
  opacity: 0.7;
  transition: all 200ms ease;
}

.slick-current .news-carousel__item {
  opacity: 1;
}

.news-carousel__item-link {
  display: block;
  text-decoration: none;
  position: relative;
  width: 100%;
}

.news-carousel__item-link:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  transition: all 200ms ease;
  opacity: 0;
  z-index: 2;
  background: linear-gradient(to bottom, rgba(9, 28, 71, 0) 0%, #091c47 100%);
}

.slick-current .news-carousel__item-link:before {
  opacity: 1;
}

.news-carousel__item-label {
  position: absolute;
  left: 15px;
  top: 15px;
  text-align: center;
  padding: 5px;
  background: rgba(255, 255, 255, 0.46);
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 14px;
  min-width: 50px;
  z-index: 4;
}

.news-carousel__item-img {
  max-width: none;
  width: 100%;
}

.news-carousel__item-name {
  font: 700 14px/18px "Roboto", sans-serif;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 0;
  padding: 15px;
  position: absolute;
  left: 0;
  bottom: 0;
  opacity: 0;
  transition: all 200ms ease;
  z-index: 3;
}

.slick-current .news-carousel__item-name {
  opacity: 1;
}

.main-nav-bg {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.31);
  display: none;
}

.main-nav {
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
  width: 300px;
  z-index: 1000;
  background: #F8F8F8;
  height: 100%;
  overflow: hidden;
  transition: all 200ms ease;
}

.main-nav.active {
  right: 0;
  transform: translateX(0);
}

.main-nav__cover {
  width: 350px;
  height: 100%;
  overflow: hidden;
  overflow-y: auto;
}

.main-nav__wrapper {
  width: 300px;
  padding: 11px 0;
}

.main-nav__close {
  display: block;
  position: absolute;
  right: 20px;
  top: 20px;
  z-index: 1;
  cursor: pointer;
}

.main-nav__icon-close {
  width: 16px;
  height: 16px;
  fill: #091C47;
}

.main-nav__wrapper > .main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  padding: 20px;
}

.main-nav__wrapper > .main-nav__list > .main-nav__item {
  font: 400 18px/23px "Gotham Pro", sans-serif;
  margin-top: 30px;
  overflow: hidden;
}

.main-nav__wrapper > .main-nav__list > .main-nav__item:first-child {
  margin-top: 0;
}

.main-nav__wrapper > .main-nav__list > .main-nav__item > .main-nav__link {
  color: #091C47;
  text-decoration: none;
  display: block;
}

.main-nav__wrapper > .main-nav__list > .main-nav__item > .main-nav__link:hover,
.main-nav__wrapper > .main-nav__list > .main-nav__item > .main-nav__link:active {
  color: #33ACEF;
}

.main-nav__wrapper > .main-nav__list .main-nav__list {
  display: none;
  position: relative;
  margin: 32px 0 5px 0;
  padding: 0 0 0 10px;
  list-style: none;
  border-left: 1px solid #D5D6D7;
}

.main-nav__wrapper > .main-nav__list .main-nav__list .main-nav__item {
  margin-top: 20px;
  font-size: 14px;
  line-height: normal;
}

.main-nav__wrapper > .main-nav__list .main-nav__list .main-nav__item:first-child {
  margin-top: 0;
}

.main-nav__wrapper > .main-nav__list .main-nav__list .main-nav__link {
  color: #4B4E55;
  text-decoration: none;
  display: block;
}

.main-nav__wrapper > .main-nav__list .main-nav__list .main-nav__link:hover,
.main-nav__wrapper > .main-nav__list .main-nav__list .main-nav__link:active {
  color: #33ACEF;
}

.modal {
  display: none;
  width: 100%;
  background: #F8F8F8;
  padding: 50px 15px 30px;
  position: relative;
}

.modal__title {
  text-align: center;
  margin: 0 auto 30px;
  font: 400 24px/25px "Roboto", sans-serif;
  color: #243138;
}

.modal__close {
  display: block;
  position: absolute;
  right: 15px;
  top: 20px;
  cursor: pointer;
}

.modal__close svg {
  width: 16px;
  height: 16px;
  fill: #091C47;
}

.form__field {
  margin-top: 27px;
}

.form__field:first-child {
  margin-top: 0;
}

.form__message {
  margin-top: 50px;
}

.form__icon {
  margin: 0 auto;
}

.form__icon__svg {
  margin: 0 auto;
  width: 61px;
}

.form__label {
  margin: 0 0 12px;
  font-size: 16px;
  line-height: normal;
  color: #243138;
}

.form__field-wrapper {
  position: relative;
}

.form__input {
  display: block;
  width: 100%;
  height: 60px;
  border: 1px solid #ECEFEF;
  background: #FFFFFF;
  padding: 0 17px;
  font: 400 16px "Roboto", sans-serif;
  color: #243138;
  outline: none;
  position: relative;
  z-index: 1;
}

.form__field_success .form__field-wrapper:before {
  content: "";
  display: block;
  width: 17px;
  height: 8px;
  border-left: 1px solid #93DF9B;
  border-bottom: 1px solid #93DF9B;
  transform: rotate(-45deg);
  position: absolute;
  right: 17px;
  top: 23px;
  z-index: 2;
}

.form__field_success .form__input {
  padding-right: 53px;
}

.form__submit {
  display: block;
  width: 100%;
  border: 2px solid #091C47;
  color: #FFFFFF;
  outline: none;
  text-align: center;
  cursor: pointer;
  transition: all 200ms ease;
  background: #091C47;
  font: 700 18px "Roboto", sans-serif;
  text-transform: uppercase;
  height: 60px;
  margin-top: 40px;
}

.form__submit:hover,
.form__submit:active {
  background: none;
  color: #091C47;
}

.video-carousel {
  position: relative;
  display: none;
}

.video-carousel__item {
  display: block !important;
  padding: 0 7.5px;
  width: 100% !important;
}

.video {
  overflow: hidden;
  position: relative;
  background: #FFFFFF;
}

.video__content {
  position: relative;
  overflow: hidden;
}

.video video {
  width: 100%;
  outline: none;
  opacity: 0.5;
  transition: all 200ms ease;
  display: block;
}

.video.play video {
  opacity: 1;
}

.video__wrapper {
  cursor: pointer;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.video.play .video__wrapper {
  opacity: 0;
  cursor: default;
}

.video__button {
  display: block;
  width: 62px;
  height: 62px;
  background: #10234E;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -31px;
  margin-top: -31px;
}

.video__button:before {
  content: "";
  display: block;
  width: 20px;
  height: 24px;
  border-left: 20px solid #FFFFFF;
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -8px;
  margin-top: -12px;
}

.video__name {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 15px;
  overflow: hidden;
  z-index: 2;
  left: 0;
  bottom: 0;
  font: 700 15px "Roboto", sans-serif;
  text-transform: uppercase;
  color: #091C47;
  text-align: center;
  transition: all 200ms ease;
}

.video-names {
  display: none;
}

.video-names__item {
  display: block !important;
}

.video-names__name {
  font: 700 22px/26px "Roboto", sans-serif;
  text-transform: uppercase;
  color: #091C47;
  margin: 0;
  position: relative;
}

.big-doctor {
  width: 100%;
  margin: 0 auto;
}

.big-doctor__cover {
  display: flex;
  align-items: center;
  position: relative;
  flex-wrap: wrap;
}

.big-doctor__img-block {
  flex: 0 0 auto;
  width: 100%;
  margin-bottom: 20px;
}

.big-doctor__img {
  max-width: none;
  width: 100%;
  box-shadow: 0 0 9px rgba(0, 0, 0, 0.2);
}

.big-doctor__content {
  text-align: center;
  width: 100%;
}

.big-doctor__name {
  font: 400 26px "Roboto", sans-serif;
  margin: 0;
  color: #091C47;
}

.big-doctor__name strong {
  font-weight: 700;
  display: block;
}

.big-doctor__status {
  color: #33ACEF;
  font: 500 16px "Gotham Pro", sans-serif;
  margin-top: 20px;
}

.card-new {
  display: flex;
  flex: 0 0 auto;
  width: 100%;
  border-radius: 6px;
  overflow: hidden;
  background: #FFFFFF;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  transition: all 200ms ease;
}

.card-new:hover,
.card-new:active {
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.card-new__link {
  display: block;
  text-decoration: none;
  overflow: hidden;
  width: 100%;
}

.card-new__img-block {
  height: 180px;
  overflow: hidden;
  position: relative;
}

.card-new__label {
  min-width: 45px;
  position: absolute;
  top: 20px;
  right: 20px;
  text-align: center;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.3);
  font: 700 9px/9px "Roboto", sans-serif;
  text-transform: uppercase;
  color: #FFFFFF;
  z-index: 2;
  padding: 5.5px;
}

.card-new__img {
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
  max-width: none;
  display: block;
}

.card-new__content {
  padding: 7px 15px 14px;
}

.card-new__date {
  text-align: right;
  font: 400 14px "PT Sans", sans-serif;
  color: #B3B3B3;
  margin-bottom: 2px;
}

.card-new__title {
  font: 400 16px/30px "PT Sans", sans-serif;
  margin: 0;
  color: #050505;
}

.content-nav {
  overflow: hidden;
  height: 16px;
  position: relative;
  margin-left: -15px;
  margin-right: -15px;
}

.content-nav__wrapper {
  overflow: hidden;
  overflow-x: auto;
  height: 50px;
}

.content-nav__list {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.content-nav__item {
  position: relative;
  display: flex;
  flex: 0 0 auto;
  white-space: nowrap;
  padding-right: 30px;
}

.content-nav__item:last-child {
  padding-right: 15px;
}

.content-nav__item:first-child {
  padding-left: 15px;
}

.content-nav__item.active {
  z-index: 2;
}

.content-nav__link {
  display: block;
  font: 400 13px "Montserrat", sans-serif;
  color: #C4C4C4;
  text-decoration: none;
  position: relative;
}

.content-nav__item.active .content-nav__link {
  color: #223359;
}

.map {
  height: 200px;
}

.map-popup + .map {
  display: none;
}

.map .map-region {
  fill: #C7D6ED;
  opacity: 0.8;
}

.map .active {
  fill: #223359;
  opacity: 0.9;
}

.map .selected {
  fill: #000;
  opacity: 1;
}

.map .map-tooltip {
  position: absolute;
  top: 100px;
  left: 200px;
  color: #fff;
  background-color: #272c31;
  width: 230px;
  font-size: 14px;
  padding: 15px 25px;
  opacity: 0;
  visibility: hidden;
  text-align: left;
}

.map .map-tooltip:before {
  content: "";
  display: block;
  position: absolute;
  background-color: #272c31;
  width: 10px;
  height: 10px;
  top: calc( 50% - 5px );
  left: -5px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.map .map-tooltip.show {
  visibility: visible;
  opacity: 1;
}

.wrapper {
  display: flex;
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  position: relative;
  flex-direction: column;
}

.wrapper__content {
  flex: 1 0 auto;
}

.wrapper__footer {
  flex: 0 0 auto;
